import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const state = { //state
  //
  userInfo: {}
};
const getters = { //state()
  userInfo(state) { //,userInfo
    return state.userInfo
  },
};
const mutations = {
  updateUserInfo(state, userInfo) { //statestate();
    state.userInfo = userInfo;
  },
};
const actions = {
  updateUserInfo(context) { //mutationscontextstore 
    context.commit('userInfo');
  },
};
const store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions
});

export default store;



